<template>
	<view class="Index">
		<!-- 悬浮按钮 -->
		<uni-fab ref="fab" :pattern="pattern" :content="content" :horizontal="horizontal" :vertical="vertical" :direction="direction" @trigger="trigger" @fabClick="fabClick"/>
		
		<!--顶部栏、轮播图、底部栏 -->
		<view class="navigationBar"></view>
		<view class="Content" hover-class="hoverClass">
			<swiper class="Swiper" indicator-dots=ture indicator-active-color=#eee5e2>
				<swiper-item>
					<image class="Image" src="/static/product/1.jpg" @click="cobit"></image>
				</swiper-item>
				<swiper-item>
					<image class="Image" src="/static/product/2.jpg" @click="bearCar"></image>
				</swiper-item>
				<swiper-item>
					<image class="Image" src="/static/product/3.jpg" @click="balanceCar"></image>
				</swiper-item>
			</swiper>
		</view>
		<view class="taBar">
			<image class="logo" src="/static/icon/internet.png"></image>
			<text>+ &nbsp;www,cokoino.com&nbsp;&nbsp;&nbsp;app v1.0</text>
		</view>
		
		<!-- 遮罩 -->
		<view v-if="maskShow" class="uni-mask uni-flex uni-column" @click="maskclose">
			<scroll-view class="uni-scroll_box" scroll-y="ture" show-scrollbar="ture">
				<view class="uni-title"> {{deviceList.length}} devices have been found </view>
				<view class="uni-list-box" v-for="(item,index) in deviceList" :key="index">
					<button class="uni-list_name" @click="onConn(item)">{{item.name}}</button>
					<view class="uni-list_item">UUID : {{item.deviceId }}, RSSI : {{ item.RSSI }}dBm</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	//引用蓝牙文件
	import Bluetooth from '@/JS/bluetooth.js'
	let bluetooth = new Bluetooth();
	//引用悬浮按钮文件
	import uniFab from '@/components/uni-fab/uni-fab.vue'
	
	//页面设定屏幕方向
	//plus.screen.lockOrientation("landscape-primary");     //仅支持横屏，Home键在右侧 
	//plus.screen.lockOrientation('landscape-secondary'); //仅支持横屏（反向），Home键在左侧
	//plus.screen.lockOrientation("portrait-primary");    //仅支持竖屏
	//plus.screen.lockOrientation("portrait-secondary");  //仅支持竖屏（反向），上下颠倒 
		
	export default {
		//定义悬浮按钮组件
		components: {
					uniFab
		},
		data() {
			return {
				//悬浮按钮参数设定
				horizontal: 'left',            //left or right
				vertical  : 'top',             //top or bottom
				direction : 'horizontal',      //vertical or horizontal
				pattern: {
					color          : '#7A7E83',  //字体颜色
					backgroundColor: '#fff',     //背景颜色
					selectedColor  : '#007AFF',  //选中时字体颜色
					buttonColor    : '#007AFF'   //悬浮按钮颜色
				},
				content: [
					{
						iconPath: '/static/bluetooth/bluetooth_off.png',
						selectedIconPath: '/static/bluetooth/bluetooth_on.png',
						text: 'BLE',
						active: false
					},
					{
						iconPath: '/static/icon/Document_write.png',
						selectedIconPath: '/static/icon/Document_write.png',
						text: 'ME',
						active: false
					}
				],
				//用于蓝牙
				currDev   : false, //蓝牙链接状态
				deviceList: [],    //List of devices
				maskShow  : false,
			}
		},
		//页面加载时
		onLoad() {
			let self = this;		
			bluetooth.openBluetoothAdapter();
		},
		//页面卸载时
		onUnload: function() {
			let self = this;
			bluetooth.closeBLEConnection();
			bluetooth.closeBluetoothAdapter();
		},
		//自定义的函数
		methods: {
			//悬浮按钮点击触发函数
			fabClick() {
				this.currDev = bluetooth.connectFlag;  //获取蓝牙链接状态
			},
			//悬浮按钮选项点击触发函数
			trigger(e) {
				let self = this
				let curDev = self.currDev
				//this.content[e.index].active = !e.item.active
				if(e.index == 0){
					if(!curDev){
						self.searchBle();
						if(curDev){
							self.content[e.index].active = true;
						}else{
							self.content[e.index].active = false;
						}	
					}else{
						self.closeBLEConnection();
						self.content[e.index].active = false;
					}
				}
				if(e.index == 1){
					uni.navigateTo({
					    url: '/pages/readme/readme'
					});
				}
			},
			
			//关闭遮罩		
			maskclose(){
				this.maskShow = false;
				this.deviceList = [];
				//停止蓝牙设备搜索
				bluetooth.stopBluetoothDevicesDiscovery();  
			},
			//搜索蓝牙设备
			searchBle() {
				let self = this
				self.deviceList = [];
				self.maskShow = true;
				setTimeout(() => {
					//开始搜寻附近的蓝牙外围设备
					bluetooth.startBluetoothDevicesDiscovery();
					//监听寻找到新设备事件
					bluetooth.onBluetoothDeviceFound();
					self.deviceList = bluetooth.deviceList;
				}, 200);
			},
			//链接蓝牙：点击列出来的蓝牙名称，执行此函数
			onConn(item) {
				bluetooth.createBLEConnection(item.deviceId);
			},
			//断开与低功耗蓝牙设备的连接
			closeBLEConnection() {
				bluetooth.closeBLEConnection();
			},
			
			//跳转页面函数
			cobit() {
				console.log("Goto cobit");
				uni.navigateTo({
				    url: '/pages/cobit/cobit'
				});
			},
			bearCar() {
				console.log("Goto bearCar");
				uni.navigateTo({
				    url: '/pages/bearCar/bearCar'
				});
			},
			balanceCar() {
				console.log("Goto balanceCar");
				uni.navigateTo({
				    url: '/pages/balanceCar/balanceCar'
				});
			}
		},
	}
</script>

<style>
	page{
		height: 100%;
	}
	.Index{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-color: #FFFFFF;
		height: 100%;
	}
	
	.navigationBar{
		background-color: #fff;
		position: absolute;
		top: 0%;
		bottom: 96%;
		left: 0;
		right: 0;
	}
	
	.Content{
		background-color: #fff;
		position: absolute;
		top: 4%;
		bottom: 4%;
		left: 0;
		right: 0;
	}
	
	.controls-title{
		width: 100rpx;
		height: 50rpx;
	}
	
	.Swiper{
		width: 750rpx;
		height: 100%;
	}
	
	.Image{
		width: 750rpx;
		height: 100%;
	}
	
	.hoverClass{
		border-radius: 200rpx;

	}
	
	.logo{
		width: 35rpx;
		height: 35rpx;
	}
	.taBar{
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: #ffffff;
		color:#646464;
		font-size: 13px;
		position: absolute;
		top: 96%;
		bottom: 0;
		left: 0;
		right: 0;
	}
	
	//遮罩
	.uni-title {
		/* width: 100%; */
		/* height: 80rpx; */
		text-align: center;
	}
	.uni-mask {
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		background: rgba(0, 0, 0, 0.6);
		padding: 0 30rpx;
		box-sizing: border-box;
	}
	.uni-scroll_box {
		width: 95%;
		height: 50%;
		background: #fff;
		border-radius: 20rpx;
	}
	.uni-list-box {
		margin: 0 20rpx;
		padding: 15rpx 0;
		border-bottom: 10px #f5f5f5 solid;
		border: 10px #f5f5f5 solid;
		box-sizing: border-box;    /*box-sizing属性允许您以特定的方式定义匹配某个区域的特定元素。*/
	}
	.uni-list_name {
		font-size: 30rpx;
		color: #333;
	}
	.uni-list_item {
		font-size: 24rpx;
		color: #555;
		line-height: 1.5;
		text-align: center;
	}
</style>
